* {
    margin: 0;
    padding: 0;

}


#principal {
    background-image: url(../../imagens/fundo.webp);
    background-color: #FFAEFF;
    background-repeat: repeat-y;
    background-size: 100%;

    width: 100%;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.conteudo {
    width: 60%;
    height: 30rem;
    text-align: center;

}

.midias img{
    height: 3rem;
    
}
.midias a:last-child{
    
    filter: invert(50%);
    filter: contrast(300%);

}

.logo img{
    height: 4.5rem;
}

.imagem-corpo img{
   
    width: 30%;
    min-width: 250px;
}

.bloco-botoes {
    
    margin: 0 auto;
    width: 15rem;
    height: 60px;

    display: flex;
    justify-content: space-between;

    position: relative;
    top: 2rem;
}


.botao {
    background: #111;
    width: 7rem;
    height: 40px;
    border-radius: .3rem;

    border: 0;
    box-shadow:#ccc 1px 5px 30px; 
    cursor: pointer;

    color: #fff;
    transition: 0.5s;
}

.botao:hover {
    background: #333;
    
}
.boneca {
    
    z-index: 999;
    right: 1rem;
    position: absolute;
    right:    0;
    bottom:   0;
    
}
.boneca img{
    height: 20rem;

    opacity: 1;
   
    -webkit-transition: opacity 10s ease-out;
    -moz-transition: opacity 10s ease-out;
    -o-transition: opacity 10s ease-out;
    -ms-transition: opacity 10s ease-out;
    transition: opacity 10s ease-out;
}



@media( min-width: 50rem){

.midias {
    grid-area: midias;
}
.midias a img{
    transition: 0.8s;
    height: 4rem;
    filter: contrast(0%)
    opacity(0.2);
    
}
.midias a:last-child{

    filter: contrast(0%);
}

.midias a img:hover, .midias a:last-child:hover {
    filter: contrast(100%);
    
}


.logo {
    grid-area: logo;
   
}
.logo img{
    min-height: 5rem;
    height: 5rem;
    
}
.imagem-corpo {
    grid-area: imagem-corpo;
    
}
.imagem-corpo img {
    min-width: 20rem;
}


.bloco-botoes {
    grid-area: bloco-botoes;
    min-width: 10rem;
}


.conteudo {
    transition: 3s;
    transform: rotateY(360deg);
    
    display: grid;
    justify-content: center;
    align-items: center;
    height: 25rem;
    min-width: 400px;
    
    grid-template-columns: 20rem 20rem;
    grid-template-rows: 3rem 8rem 5rem;
    grid-template-areas: "midias imagem-corpo"
                         "logo imagem-corpo"
                         "bloco-botoes imagem-corpo";
}
}